import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NotificationService } from 'src/app/modules/main/services/notification.service';
import { IUserRegister } from 'src/app/shared/interfaces/i-auth';
import { AuthService } from '../../services/auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.scss']
})
export class RegisterComponent implements OnInit {
// test: string
email!:string
password!:string
userData!: IUserRegister

logInForm: FormGroup;
hide = true;
constructor(private authService: AuthService, 
            private router: Router, 
            private formB: FormBuilder,
            private notiSvc: NotificationService) {
  this.buildForm();
}

ngOnInit(): void {
}

  register(): void{
    if(this.logInForm.invalid){
      this.notiSvc.openSnackBar("Favor de introducir los datos correctamente", 2000)
      return ;
    } else if (this.logInForm.get('email')?.value != this.logInForm.get('email_confirmation')?.value) {
      this.notiSvc.openSnackBar("Los correos deben de coincidir", 2000)
      return ;
    } else if (this.logInForm.get('password')?.value != this.logInForm.get('password_confirmation')?.value) {
      this.notiSvc.openSnackBar("Las contraseÃ±as deben de coincidir", 2000)
      return ;
    }
    this.setAuthData()
    this.authService.onRegister(this.userData).subscribe( () => {
        console.log("pog")
        this.router.navigate(['auth/login']);
        this.notiSvc.openSnackBar("Registrado correctamente", 2000)
      }, error => {
        console.log(error);
        this.notiSvc.openSnackBar("Hubo un error", 2000)
      });
  }

  buildForm(): void {
    this.logInForm = this.formB.group({
      username: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required],
      email_confirmation: ['', Validators.required],
      password_confirmation: ['',Validators.required]
    });
  }

  setAuthData(): void{
    this.userData = {
      username: this.logInForm.get('username')?.value,
      email: this.logInForm.get('email')?.value,
      password: this.logInForm.get('password')?.value,
      email_confirmation: this.logInForm.get('email_confirmation')?.value,
      password_confirmation: this.logInForm.get('password_confirmation')?.value,
    };
  }
}
